var _self, logRes , USERID,myInfo,product,address,pnumb,formdata
export default {
	data() {
		return {
			product: [],
			myInfo:[],
			pnumb:null
		};
	},
	onLoad(options) {
		_self = this;
				logRes = _self.checkLogin('../Buy/Buy',1);
				_self.product = JSON.parse(options.product);
				_self.pnumb = options.pnum/* uni.getStorageSync('OPnum') */;
				console.log(_self.pnumb);
				console.log(_self.product);
				_self.USERID = logRes[0];
				
				uni.request({
					url: _self.apiServer+'mall/order/myinfo',
					method: 'GET',
					data: {
						userid : _self.USERID
					},
					success: res => {
						_self.myInfo = res.data.data;
						console.log(_self.myInfo);
					},
					fail: () => {},
					complete: () => {}
				});
			
	        },
	methods: {
		formSubmit: function(e) {
		                // console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
		                formdata = e.detail.value 
						// console.log(formdata)
						if(formdata.pnum==undefined){
							uni.showToast({
								title:'购买数量不能为空',
								duration: 1000
							});
							setTimeout(function(){
								uni.reLaunch({
											    url: '../order'
											});
								},1000)
						}else{
							uni.request({
								url: _self.apiServer+'mall/order/buy',
								method: 'POST',
								header: {
									'content-type': 'application/x-www-form-urlencoded', 
								},
								data: {
									pid				:  formdata.pid,
									pnum			:  formdata.pnum,
									userid		:  formdata.userid,
									uaddress :  formdata.uaddress,
									uusername	:  formdata.uusername ,
									uphonenum   :  formdata.uphonenum
								},
								success: res => {
									var result = res.data.data;
									if(result==200){
										uni.showToast({
											title:'操作成功',
											duration: 1000,
											icon:'none'
										});
										setTimeout(function(){
											uni.reLaunch({
														    url: '../../product/product'
														});
											},1000)
									}else{
										uni.showToast({
											title:'积分不足，购买失败',
											duration: 1000,
											icon:'none'
										});
										setTimeout(function(){
											uni.reLaunch({
														    url: '../../product/product'
														});
											},1000)
									}
								},
								fail: () => {},
								complete: () => {}
							});
						}
						
		            },
		retu(){
			// uni.showToast({
			// 	title:'操作成功',
			// 	duration: 1000
			// });
			
			setTimeout(function(){
				uni.reLaunch({
							    url: '../../product/product'
							});
				})
		}
	}
}
